      console.log(this.showHeight);
      console.log(this.availableHeight) ;
    },

    Component(e){
      this.currentComponent = e;
    },

    viewDetails(id){
        let message = _.find(this.messages, {id:id});
        this.selectedMessage = message;
      },
    },
}
