<template lang="html">
  <div class="grid-prefs" v-bind:style="{ 'height': availableHeight+'px' }">

    <div class="menu-prefs">
      <TopMenu></TopMenu>
      <SideMenu></SideMenu>
    </div>

    <div class="main-prefs">
        <component
        :is="currentComponent">
        </component>
    </div>


  </div>
</template>

<script>
import TopMenu from '../components/Edit/TopMenu/TopMenu'
import SideMenu from '../components/Edit/SideMenu/SideMenu'
import General from '../components/Edit/MainContent/General'
export default {
  data(){
    return{
      availableHeight:{},
      currentComponent: 'General'
    }
  },
  components: {
    TopMenu,
    SideMenu,
    General
  },
  mounted(){
    this.matchHeight();
  },
  methods: {
    matchHeight () {
      var availableHeight = window.innerHeight - $('.page-container').offset().top - $('.navbar-fixed-bottom').outerHeight();
      this.availableHeight = availableHeight;
    },
  }
}
</script>

<style lang="css" scoped>
.grid-prefs{
  display: grid;
  grid-template-columns: minmax(240px,240px) 4fr;
}
.menu-prefs{
  max-height: 100%;
  overflow: hidden;
}

</style>
