<template lang="html">
  <li>
  <div
    :class="{bold: isFolder}"
    @click="toggle"
    @dblclick="changeType">
    {{ model.name }}
    <span v-if="isFolder">[{{ open ? '-' : '+' }}]</span>
  </div>
  <ul v-show="open" v-if="isFolder">
    <testitem
      class="item"
      v-for="(model, index) in model.children"
      :key="index"
      :model="model">
    </testitem>
    <li class="add" @click="addChild">+</li>
  </ul>
</li>
</template>

<script>
export default {
    name: 'testitem',
    props: {
    model: Object
    },
    data(){
      return {
        open: false
      }
    },
    computed: {
      isFolder: function () {
        return this.model.children &&
          this.model.children.length
      }
    },
    methods: {
      toggle: function () {
        if (this.isFolder) {
          this.open = !this.open
        }
      },
      changeType: function () {
        if (!this.isFolder) {
          this.$set(this.model, 'children', [])
          this.addChild()
          this.open = true
        }
      },
      addChild: function () {
        this.model.children.push({
          name: 'new stuff'
        })
      }
    }
  }
</script>

<style lang="css" scoped>
.item {
  cursor: pointer;
}
.bold {
  font-weight: bold;
}
</style>
